import React from 'react'

function PageFour({  formData, setFormData }) {
  return (
    <div className='pageFour_form'>
      <h2>Dados do imvel</h2>
      <p>Confirme o imvel para qual voc deseja preencher os dados:</p>
      <div className='pageFour_form_row form_input'>
        <p>Imvel: <span className='inpunt__required'>*</span></p>
        <select
          value={formData.houseType}
          onChange={(e) => {
            setFormData({...formData, houseType: e.target.value});
          }}
          required
        >
          <option
            name="houseType"
            value=""
          >
            Selecionar...
          </option>
          <option
            name="houseType"
            value="Opo 1"
          >
            Opo 1
          </option>
          <option
            name="houseType"
            value="Opo 2"
          >
            Opo 2
          </option>
        </select>
      </div>
      <div className='pageFour_form_row'>
        <p>Tipo de cadastro <span className='inpunt__required'>*</span></p>
        <div>
          <input
            type="radio"
            name="personType"
            id="PessoaFsica"
            value="Pessoa Fsica"
            checked={formData.personType === "Pessoa Fsica" ? true : false}
            onChange={(e) => {
              setFormData({...formData, personType: e.target.value});
            }}
            required
          />
          <label htmlFor='PessoaFsica'>Pessoa Fsica</label>
        </div>
        <div>
          <input
            type="radio"
            name="personType"
            id="PessoaJurdica"
            value="Pessoa Jurdica"
            checked={formData.personType === "Pessoa Jurdica" ? true : false}
            onChange={(e) => {
              setFormData({...formData, personType: e.target.value});
            }}
            required
          />
          <label htmlFor='PessoaJurdica'>Pessoa Jurdica</label>
        </div>
      </div>
    </div>
  )
}

export default PageFour